.App {
  display: grid;
  place-items: center;
  height: 100vh;
  background-color:#dadbd3;
}

*{
  outline: 0;
}
.app_body{
  display: flex;
  background: #1b1b1b;
  margin-top: 0px;
  height: 90vh -webkit-max-content;
  height: 90vh -moz-max-content;
  height: 90vh max-content;
  width: 90vw;

  overflow: hidden!important;

}

.sidebar{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex: 0.3 1;
  background: #1b1b1b;
  color: white;
  padding: 30px;
  border-right: 2px solid #292929;
}

.sidebar .logo{
  width: 3vw;
  height: 6vh;
}

.sidebar .logoarea{

  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-family: 'Roboto', sans-serif;
  margin-bottom: 20px;
}
.sidebar .logoarea h4{
  font-weight: 200;
  margin: 10px;
  color: lightgray;
}

.sidebar .sidebaritems{
  
  display: flex;
  flex-direction: column;

}

.sidebar .sidebaritems button{
  border: none;
  background: none;
  color: gray;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  font-size: 15px;

  margin: 10px;

}

.sidebar .sidebaritems button:hover{
  cursor: pointer;
  color: white;


}

.sidebar .sidebaritems button svg{
  margin-right: 20px;
  font-size: 20px;
}


.sidebar .sidebaritems_info{
  margin-top: 180px;
  display: flex;
  flex-direction: column;

}

.sidebar .sidebaritems_info button{
  border: none;
  background: none;
  color: gray;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  font-size: 15px;

  margin: 10px;
