.badge{
  position: absolute;
  bottom: 0px;
  left: 20px;
  padding: 4px 4px;
  border-radius: 180px;
 
  background-color: #00ff00;
  color: white;
}

.store{
  flex: 1 1;
}

.cards{
  padding: 40px;
  font-family: 'Roboto', sans-serif;
  color: white;
  display: flex;
  flex-wrap: wrap;
  overflow-y: scroll;
  max-height: 500px;
  margin: 10px;
}

.cards::-webkit-scrollbar {
  width: 10px;
 
}

/* Track */
.cards::-webkit-scrollbar-track {
  background: transparent; 
  
}
 
/* Handle */
.cards::-webkit-scrollbar-thumb {
  background: #fff; 
  border-radius: 180px;

}

/* Handle on hover */
.cards::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
.carditem{
  margin: 10px;
  transition: 0.5s ease;
  margin-bottom: 40px;

}

.carditem span{
  font-size: 12px;
  color: gray;

}

.carditem .play{

  -webkit-filter: grayscale(0%);

          filter: grayscale(0%);

}
.carditem img{
  border-radius: 5px;
  width: 10vw;
  height: 30vh;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  transition: 0.5s ease;
}

.carditem:hover{
  transform: scale(1) ;
  cursor: pointer;
}

.carditem:hover img{
  -webkit-filter: grayscale(0);
